import BackspaceIcon from "@mui/icons-material/Backspace";
import { Button, Fade, InputLabel, MenuItem, Select, SelectChangeEvent, Snackbar, TextField } from "@mui/material";
import React, { useCallback, useEffect, useReducer, useState } from "react";
import { ToWords } from "to-words";
import allwords from "./allwords.txt";
import "./App.css";
import words from "./words.txt";

const minWordLen = 4;
const maxWordLen = 7;

const toWords = new ToWords();

function App() {
  const wordsMap = getWordsMap(words as string, minWordLen, maxWordLen);
  const allwordsMap = getWordsMap(allwords as string, minWordLen, maxWordLen);
  removeWordsNotInAllwords(wordsMap, allwordsMap);
  return <Wordle wordsMap={wordsMap} allwordsMap={allwordsMap}></Wordle>;
}

function getWordsMap(wordsString: string, minLen: number, maxLen: number) {
  const wordsMap = new Map<number, string[]>();
  for (let i = minLen; i <= maxLen; i++) {
    wordsMap.set(i, []);
  }
  const listOfWords = wordsString.split(/\s+/);
  for (const word of listOfWords) {
    if (word.length >= minLen && word.length <= maxLen) {
      wordsMap.get(word.length)!.push(word.toUpperCase());
    }
  }
  return wordsMap;
}

function removeWordsNotInAllwords(wordsMap: Map<number, string[]>, allwordsMap: Map<number, string[]>) {
  for (let i = minWordLen; i <= maxWordLen; i++) {
    const filteredWords: string[] = [];
    for (let j = 0; j < wordsMap.get(i)!.length; j++) {
      if (allwordsMap.get(i)!.includes(wordsMap.get(i)![j])) {
        filteredWords.push(wordsMap.get(i)![j]);
      }
    }
    wordsMap.set(i, filteredWords);
  }
}

type WordleState = {
  numGuesses: number;
  numCharacters: number;
  guesses: Guess[];
  characterStatusMap: Map<string, CharacterStatus>;
  word: string;
  characterIndicesMap: Map<string, Set<number>>;
  gameState: GameState;
  currentGuessIndex: number;
  currentCharacterIndex: number;
  notificationOpen: boolean;
  message: string;
};

type WordleAction =
  | { type: "numGuesses.update"; newNumGuesses: number }
  | { type: "numCharacters.update"; newNumCharacters: number }
  | { type: "gameState.update"; newGameState: GameState; newWord?: string }
  | { type: "snackbar.show"; message: string }
  | { type: "snackbar.hide" }
  | { type: "backspace.pressed" }
  | { type: "enter.pressed" }
  | { type: "aToZ.pressed"; key: string };

const wordleReducer = (state: WordleState, action: WordleAction): WordleState => {
  switch (action.type) {
    case "numGuesses.update":
      return {
        ...state,
        numGuesses: action.newNumGuesses,
        guesses: getInitialGuesses(action.newNumGuesses, state.numCharacters),
      };
    case "numCharacters.update":
      return {
        ...state,
        numCharacters: action.newNumCharacters,
        guesses: getInitialGuesses(state.numGuesses, action.newNumCharacters),
      };
    case "gameState.update":
      return getUpdatedStateAfterGameStateUpdate(state, action.newGameState, action.newWord);
    case "snackbar.show":
      return { ...state, message: action.message, notificationOpen: true };
    case "snackbar.hide":
      return { ...state, notificationOpen: false };
    case "backspace.pressed": {
      const updatedGuesses = [...state.guesses];
      updatedGuesses[state.currentGuessIndex].guessCharacters[state.currentCharacterIndex - 1] = { character: "" };
      return { ...state, guesses: updatedGuesses, currentCharacterIndex: state.currentCharacterIndex - 1 };
    }
    case "enter.pressed":
      return getUpdatedStateAfterEnterPressed(state);
    case "aToZ.pressed": {
      const updatedGuesses = [...state.guesses];
      updatedGuesses[state.currentGuessIndex].guessCharacters[state.currentCharacterIndex] = {
        character: action.key,
        status: "unknown",
      };
      return { ...state, guesses: updatedGuesses, currentCharacterIndex: state.currentCharacterIndex + 1 };
    }
    default: {
      throw new Error();
    }
  }
};

function getInitialGuesses(numGuesses: number, numCharacters: number) {
  const guesses: Guess[] = [];
  for (let i = 0; i < numGuesses; i++) {
    const guessCharacters: GuessCharacter[] = [];
    for (let j = 0; j < numCharacters; j++) {
      guessCharacters.push({ character: "" });
    }
    guesses.push({ guessCharacters });
  }
  return guesses;
}

function getInitialCharacterStatusMap() {
  const characterStatusMap = new Map<string, CharacterStatus>();
  for (let ch = 0; ch < 26; ch++) {
    characterStatusMap.set(String.fromCharCode(65 + ch), "unknown");
  }
  return characterStatusMap;
}

function getCharacterIndicesMapForWord(word: string) {
  const characterIndicesMapForWord = new Map<string, Set<number>>();
  for (let i = 0; i < word.length; i++) {
    if (!characterIndicesMapForWord.has(word[i])) {
      characterIndicesMapForWord.set(word[i], new Set<number>());
    }
    characterIndicesMapForWord.get(word[i])!.add(i);
  }
  return characterIndicesMapForWord;
}

function getUpdatedStateAfterGameStateUpdate(
  state: WordleState,
  newGameState: GameState,
  newWord?: string
): WordleState {
  const updatedState = { ...state, gameState: newGameState };
  if (newGameState === "new") {
    return {
      ...updatedState,
      guesses: getInitialGuesses(state.numGuesses, state.numCharacters),
      characterStatusMap: getInitialCharacterStatusMap(),
      currentGuessIndex: 0,
      currentCharacterIndex: 0,
    };
  } else if (newGameState === "in_progress") {
    return {
      ...updatedState,
      word: newWord!,
      characterIndicesMap: getCharacterIndicesMapForWord(newWord!),
    };
  }
  return updatedState;
}

function getUpdatedStateAfterEnterPressed(state: WordleState): WordleState {
  const updatedGuesses = [...state.guesses];
  const updatedCharacterIndicesMap = getUpdatedCharacterIndicesMap(state);
  const updatedCharacterStatusMap = new Map(state.characterStatusMap);
  let numCorrect = 0;
  for (let i = 0; i < state.numCharacters; i++) {
    const currentCharacter = updatedGuesses[state.currentGuessIndex].guessCharacters[i];
    if (state.word[i] === currentCharacter.character) {
      currentCharacter.status = "correct";
      numCorrect++;
    } else if (updatedCharacterIndicesMap.has(currentCharacter.character)) {
      currentCharacter.status = "wrong_position";
    } else {
      currentCharacter.status = "not_used";
    }
    if (
      currentCharacter.status === "correct" ||
      currentCharacter.status === "not_used" ||
      updatedCharacterStatusMap.get(currentCharacter.character) === "unknown"
    ) {
      updatedCharacterStatusMap.set(currentCharacter.character, currentCharacter.status!);
    }
  }
  const updatedState = {
    ...state,
    guesses: updatedGuesses,
    characterIndicesMap: updatedCharacterIndicesMap,
    characterStatusMap: updatedCharacterStatusMap,
    currentGuessIndex: state.currentGuessIndex + 1,
    currentCharacterIndex: 0,
  };
  if (numCorrect === state.numCharacters) {
    return { ...updatedState, gameState: "won", message: "Well done!", notificationOpen: true };
  } else if (state.currentGuessIndex === state.numGuesses - 1) {
    return { ...updatedState, gameState: "lost", message: state.word, notificationOpen: true };
  }
  return updatedState;
}

function getUpdatedCharacterIndicesMap(state: WordleState) {
  const updatedCharacterIndicesMap = new Map(state.characterIndicesMap);
  for (let i = 0; i < state.numCharacters; i++) {
    if (
      state.word[i] === state.guesses[state.currentGuessIndex].guessCharacters[i].character &&
      updatedCharacterIndicesMap.has(state.word[i])
    ) {
      updatedCharacterIndicesMap.get(state.word[i])!.delete(i);
      if (updatedCharacterIndicesMap.get(state.word[i])!.size === 0) {
        updatedCharacterIndicesMap.delete(state.word[i]);
      }
    }
  }
  return updatedCharacterIndicesMap;
}

function getInitialState(): WordleState {
  if (window.localStorage.getItem("wordleState")) {
    return JSON.parse(window.localStorage.getItem("wordleState")!, reviver) as WordleState;
  }
  return {
    numGuesses: 6,
    numCharacters: 5,
    guesses: getInitialGuesses(6, 5),
    characterStatusMap: getInitialCharacterStatusMap(),
    word: "",
    characterIndicesMap: new Map<string, Set<number>>(),
    gameState: "new",
    currentGuessIndex: 0,
    currentCharacterIndex: 0,
    notificationOpen: false,
    message: "",
  };
}

function replacer(_: string, value: any) {
  if (value instanceof Map) {
    return {
      dataType: "Map",
      value: Array.from(value.entries()),
    };
  } else {
    return value;
  }
}

function reviver(_: string, value: any) {
  if (typeof value === "object" && value !== null) {
    if (value.dataType === "Map") {
      return new Map(value.value);
    }
  }
  return value;
}

const Wordle: React.FC<{ wordsMap: Map<number, string[]>; allwordsMap: Map<number, string[]> }> = ({
  wordsMap,
  allwordsMap,
}) => {
  const [numGuessesInput, setNumGuessesInput] = useState("6");
  const [state, dispatch] = useReducer(wordleReducer, getInitialState());

  useEffect(() => {
    window.localStorage.setItem("wordleState", JSON.stringify(state, replacer));
  }, [state]);

  const handleEnter = useCallback(() => {
    if (state.currentCharacterIndex !== state.numCharacters) {
      dispatch({ type: "snackbar.show", message: "Not enough letters" });
      return;
    }
    const guess = state.guesses[state.currentGuessIndex].guessCharacters
      .map((guessCharacter) => guessCharacter.character)
      .join("");
    if (!allwordsMap.get(state.numCharacters)!.includes(guess)) {
      dispatch({ type: "snackbar.show", message: "Not in word list" });
      return;
    }
    dispatch({ type: "enter.pressed" });
  }, [state.currentCharacterIndex, state.numCharacters, state.guesses, state.currentGuessIndex, allwordsMap]);
  const handleKeyupEvent = useCallback(
    (event: KeyboardEvent) => {
      if (state.gameState === "in_progress") {
        if (event.keyCode === 8 && state.currentCharacterIndex > 0) {
          dispatch({ type: "backspace.pressed" });
        } else if (event.keyCode === 13) {
          handleEnter();
        } else if (event.keyCode >= 65 && event.keyCode <= 90 && state.currentCharacterIndex < state.numCharacters) {
          dispatch({ type: "aToZ.pressed", key: event.key.toUpperCase() });
        }
      }
    },
    [state.gameState, state.currentCharacterIndex, handleEnter, state.numCharacters]
  );
  const handleKeydownEvent = (event: KeyboardEvent) => {
    if (event.keyCode === 13) {
      event.preventDefault();
    }
  };

  useEffect(() => {
    document.addEventListener("keyup", handleKeyupEvent);

    return () => {
      document.removeEventListener("keyup", handleKeyupEvent);
    };
  }, [handleKeyupEvent]);

  useEffect(() => {
    document.addEventListener("keydown", handleKeydownEvent);

    return () => {
      document.removeEventListener("keydown", handleKeydownEvent);
    };
  }, []);

  const onGuessesChanged = (event: React.ChangeEvent<HTMLInputElement>) => {
    setNumGuessesInput(event.target.value);
    const valueAsNumber = Number(event.target.value);
    if (!isNaN(valueAsNumber) && valueAsNumber === Math.round(valueAsNumber)) {
      dispatch({ type: "numGuesses.update", newNumGuesses: valueAsNumber });
    }
  };
  const onCharactersChanged = (event: SelectChangeEvent<number>) => {
    dispatch({ type: "numCharacters.update", newNumCharacters: event.target.value as number });
  };
  const share = () => {
    navigator.clipboard.writeText(getShareableResult());
    dispatch({ type: "snackbar.show", message: "Copied results to clipboard" });
  };
  const getShareableResult = () => {
    let result = "Wordle+: ";
    if (state.gameState === "won") {
      result += state.currentGuessIndex;
    } else if (state.gameState === "lost") {
      result += "X";
    }
    result += "/";
    result += state.numGuesses;
    result += "\n";
    for (let i = 0; i < state.currentGuessIndex; i++) {
      result += state.guesses[i].guessCharacters
        .map((guessCharacter) => {
          if (guessCharacter.status === "not_used") {
            return "";
          } else if (guessCharacter.status === "wrong_position") {
            return "";
          } else if (guessCharacter.status === "correct") {
            return "";
          }
          return "";
        })
        .join("");
      result += "\n";
    }
    return result;
  };
  const start = () => {
    const listOfWords = wordsMap.get(state.numCharacters)!;
    const randomWord = listOfWords[Math.floor(Math.random() * listOfWords.length)];
    const urlParams = new URLSearchParams(window.location.search);
    if (urlParams.get("debug") === "true") {
      console.log(randomWord);
    }
    dispatch({ type: "gameState.update", newGameState: "in_progress", newWord: randomWord });
  };
  const reset = () => {
    dispatch({ type: "gameState.update", newGameState: "new" });
  };

  return (
    <div className="wordle">
      <div className="content_container">
        <div className="title">WORDLE+</div>
        <ToolbarComponent
          gameState={state.gameState}
          numGuesses={numGuessesInput}
          onGuessesChanged={onGuessesChanged}
          numCharacters={state.numCharacters}
          onCharactersChanged={onCharactersChanged}
          share={share}
          start={start}
          reset={reset}
        ></ToolbarComponent>
        <GuessesComponent guesses={state.guesses}></GuessesComponent>
      </div>
      <KeyboardComponent
        characterStatusMap={state.characterStatusMap}
        onClickCharacter={(character) => {
          if (state.gameState === "in_progress" && state.currentCharacterIndex < state.numCharacters) {
            dispatch({ type: "aToZ.pressed", key: character });
          }
        }}
        onClickEnter={() => {
          if (state.gameState === "in_progress") {
            handleEnter();
          }
        }}
        onClickBackspace={() => {
          if (state.gameState === "in_progress" && state.currentCharacterIndex > 0) {
            dispatch({ type: "backspace.pressed" });
          }
        }}
      ></KeyboardComponent>
      <Snackbar
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        autoHideDuration={1000}
        transitionDuration={{ enter: 0 }}
        TransitionComponent={Fade}
        open={state.notificationOpen}
        onClose={(_, reason) => {
          if (reason === "timeout") {
            dispatch({ type: "snackbar.hide" });
          }
        }}
        sx={{ zIndex: state.notificationOpen ? "" : -1 }}
      >
        <div className="notification_message">{state.message}</div>
      </Snackbar>
    </div>
  );
};

type GameState = "new" | "in_progress" | "won" | "lost";

const ToolbarComponent: React.FC<{
  gameState: GameState;
  numGuesses: string;
  onGuessesChanged: (event: React.ChangeEvent<HTMLInputElement>) => void;
  numCharacters: number;
  onCharactersChanged: (event: SelectChangeEvent<number>) => void;
  share: () => void;
  start: () => void;
  reset: () => void;
}> = ({ gameState, numGuesses, onGuessesChanged, numCharacters, onCharactersChanged, share, start, reset }) => {
  return (
    <div className="toolbar">
      <div className="toolbar_left">
        {gameState === "new" && (
          <div>
            <div className="num_guesses_setting">
              <InputLabel>Number of Guesses:</InputLabel>
              <TextField
                size="small"
                value={numGuesses}
                onChange={onGuessesChanged}
                sx={{ marginLeft: "8px", width: 50 }}
              ></TextField>
            </div>
            <div className="num_characters_setting">
              <InputLabel>Word Length:</InputLabel>
              <Select size="small" value={numCharacters} onChange={onCharactersChanged}>
                {Array.from({ length: maxWordLen - minWordLen + 1 }, (_, i) => i + minWordLen).map((i) => (
                  <MenuItem key={i} value={i}>
                    {toWords.convert(i)}
                  </MenuItem>
                ))}
              </Select>
            </div>
          </div>
        )}
        {(gameState === "won" || gameState === "lost") && (
          <Button
            variant="contained"
            size="small"
            onClick={share}
            sx={{
              marginTop: "8px",
              marginBottom: "8px",
              background: "#6aaa64",
              ":hover": {
                background: "#6aaa64",
                opacity: 0.9,
              },
            }}
          >
            Share
          </Button>
        )}
      </div>
      <div className="spacer"></div>
      <div className="toolbar_right">
        {gameState === "new" ? (
          <Button variant="contained" size="small" onClick={start} sx={{ marginTop: "8px", marginBottom: "8px" }}>
            Start
          </Button>
        ) : (
          <Button variant="contained" size="small" onClick={reset} sx={{ marginTop: "8px", marginBottom: "8px" }}>
            Reset
          </Button>
        )}
      </div>
    </div>
  );
};

type Guess = {
  guessCharacters: GuessCharacter[];
};

type GuessCharacter = {
  character: string;
  status?: CharacterStatus;
};

const GuessesComponent: React.FC<{ guesses: Guess[] }> = ({ guesses }) => {
  return (
    <div className="guesses">
      {guesses.map((guess, i) => (
        <GuessComponent key={`guess_${i}`} guess={guess}></GuessComponent>
      ))}
    </div>
  );
};

const GuessComponent: React.FC<{ guess: Guess }> = ({ guess }) => {
  return (
    <div className="guess_row">
      {guess.guessCharacters.map((guessCharacter, i) => (
        <GuessCharacterComponent
          key={`guess_character_${i}`}
          guessCharacter={guessCharacter}
          numCharacters={guess.guessCharacters.length}
        ></GuessCharacterComponent>
      ))}
    </div>
  );
};

const GuessCharacterComponent: React.FC<{ guessCharacter: GuessCharacter; numCharacters: number }> = ({
  guessCharacter,
  numCharacters,
}) => {
  return guessCharacter.character === "" ? (
    <div className={`guess_character_div_${numCharacters} guess_character_empty`}></div>
  ) : guessCharacter.status === "unknown" ? (
    <div className={`guess_character_div_${numCharacters} guess_character guess_character_unknown`}>
      {guessCharacter.character}
    </div>
  ) : (
    <div
      className={`guess_character_div_${numCharacters} guess_character guess_character_${guessCharacter.status} status_${guessCharacter.status}`}
    >
      {guessCharacter.character}
    </div>
  );
};

const KeyboardComponent: React.FC<{
  characterStatusMap: Map<string, CharacterStatus>;
  onClickCharacter: (character: string) => void;
  onClickEnter: () => void;
  onClickBackspace: () => void;
}> = ({ characterStatusMap, onClickCharacter, onClickEnter, onClickBackspace }) => {
  const rows = [
    ["Q", "W", "E", "R", "T", "Y", "U", "I", "O", "P"],
    ["A", "S", "D", "F", "G", "H", "J", "K", "L"],
    ["Z", "X", "C", "V", "B", "N", "M"],
  ];
  return (
    <div className="keyboard">
      {rows.map((r, i) => (
        <div key={`keyboard_row_${i}`} className="keyboard_row">
          {i === rows.length - 1 && <KeyboardEnterComponent onClick={onClickEnter}></KeyboardEnterComponent>}
          {r.map((ch) => (
            <KeyboardCharacterComponent
              key={`keyboard_character_${ch}`}
              character={ch}
              status={characterStatusMap.get(ch)!}
              onClick={() => onClickCharacter(ch)}
            ></KeyboardCharacterComponent>
          ))}
          {i === rows.length - 1 && (
            <KeyboardBackspaceComponent onClick={onClickBackspace}></KeyboardBackspaceComponent>
          )}
        </div>
      ))}
    </div>
  );
};

type CharacterStatus = "unknown" | "not_used" | "wrong_position" | "correct";

const KeyboardCharacterComponent: React.FC<{
  character: string;
  status: CharacterStatus;
  onClick: () => void;
}> = ({ character, status, onClick }) => {
  return (
    <div className={`keyboard_character status_${status}`} onClick={onClick}>
      {character}
    </div>
  );
};

const KeyboardEnterComponent: React.FC<{ onClick: () => void }> = ({ onClick }) => {
  return (
    <div className="keyboard_character keyboard_character_wide status_unknown" onClick={onClick}>
      Enter
    </div>
  );
};

const KeyboardBackspaceComponent: React.FC<{ onClick: () => void }> = ({ onClick }) => {
  return (
    <div className="keyboard_character keyboard_character_wide status_unknown" onClick={onClick}>
      <BackspaceIcon></BackspaceIcon>
    </div>
  );
};

export default App;
